<style scoped>
.layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
    height: 540px;
}
.layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
}
.layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
}
.layout-footer-center{
    text-align: center;
}
</style>
<template>
    <div class="layout">
        <Card :style="{width:'50%',margin:'auto','margin-top':'5%'}">
            <p slot="title">登陆</p>
            <Layout :style="{padding: '0 50px',background:'white'}">
                <Breadcrumb :style="{margin: '16px 0'}">
                    <BreadcrumbItem></BreadcrumbItem>
                </Breadcrumb>
                <Row type="flex" justify="center" class="code-row-bg">
                    <Col :style="{'text-align': 'center'}" span="24">
                        <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
                            <Row>
                              <FormItem :style="{width:'50%'}" prop="user">
                                  <Input type="text" v-model="formInline.user" placeholder="Username">
                                      <Icon type="ios-person-outline" slot="prepend"></Icon>
                                  </Input>
                              </FormItem>
                            </Row>
                            <Row>
                              <FormItem :style="{width:'50%'}" prop="password">
                                  <Input type="password" v-model="formInline.password" placeholder="Password">
                                      <Icon type="ios-locked-outline" slot="prepend"></Icon>
                                  </Input>
                              </FormItem>
                            </Row>
                            <FormItem >
                                <Button type="primary" @click="handleSubmit('formInline')">登录</Button>
                            </FormItem>
                        </Form>
                    </Col>
                </Row>
            </Layout>
        </Card>
    </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      formInline: {
        user: '',
        password: ''
      },
      ruleInline: {
        user: [
          { required: true, message: '请填写用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请填写密码', trigger: 'blur' },
          { type: 'string', min: 6, message: '密码长度不能小于6位', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleSubmit (name) {
      console.log(this.formInline.user);
      this.$http.jsonp('localhost:3000/api/create1',{name:'123'}).then(
            function (res) {
                // 处理成功的结果
                alert(res.body);
            },
            function (res) {
            // 处理失败的结果
            });
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Message.success('提交成功!')
        } else {
          this.$Message.error('表单验证失败!')
        }
      })
    }
  }
}
</script>



